import time_helper from "@/common/js/utils/time_helper";

function commodity_approve_email(commodity, censor, owner) {
    let header = `${owner.nickname}:\r\n`;
    let body = `"${commodity.name}"${commodity.type}\r\n`;
    let footer = `${censor.realname}\r\n${time_helper.convert_date_to_date_time_string(new Date())}`;
    return header + body + footer;
}

function commodity_reject_email(commodity, censor, owner, reason) {
    let header = `${owner.nickname}:\r\n`;
    let body = `"${commodity.name}"${commodity.type}\r\n\r\n${reason}\r\n`;
    let footer = `${censor.realname}\r\n${time_helper.convert_date_to_date_time_string(new Date())}`;
    return header + body + footer;
}

function transaction_create_email(commodity, buyer, owner, detail) {
    let header = `${owner.nickname}\r\n`;
    let body = `(${buyer.nickname})"${commodity.name}"(${commodity.type})\r\n`
    let postscript = `${detail}\r\n`;
    let footer = `\r\n${buyer.tel}\r\nemail${buyer.email}\r\nQQ${buyer.QQ}\r\n${time_helper.convert_date_to_date_time_string(new Date())}`;
    return header + body + postscript + footer;
}

function transaction_owner_reject_email(commodity, buyer, owner) {
    let header = `${buyer.nickname}\r\n`;
    let body = `(${owner.nickname})"${commodity.name}"(${commodity.type})\r\n`;
    let footer = `\r\n${owner.tel}\r\nemail${owner.email}\r\nQQ${owner.QQ}\r\n${time_helper.convert_date_to_date_time_string(new Date())}`;
    return header + body + footer;
}

function transaction_owner_accept_email(commodity, buyer, owner) {
    let header = `${buyer.nickname}\r\n`;
    let body = `(${owner.nickname})"${commodity.name}"(${commodity.type})\r\n`;
    let footer = `\r\n${owner.tel}\r\nemail${owner.email}\r\nQQ${owner.QQ}\r\n${time_helper.convert_date_to_date_time_string(new Date())}`;
    return header + body + footer;
}

function requirement_solution_email(commodity, requirement_creator, requirement, solver) {
    let header = `${requirement_creator.nickname}\r\n`;
    let body = `(${solver.nickname})"${commodity.name}"(${commodity.type})${requirement.commodity_type}\r\n`;
    let footer = `\r\n${solver.tel}\r\nemail${solver.email}\r\nQQ${solver.QQ}\r\n${time_helper.convert_date_to_date_time_string(new Date())}`;
    return header + body + footer;
}

export default {
    commodity_approve_email,
    commodity_reject_email,
    transaction_create_email,
    transaction_owner_reject_email,
    transaction_owner_accept_email,
    requirement_solution_email
}